import React from 'react';
import DeleteIcon from '@mui/icons-material/Delete';

import { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';


export default function Cart() {
    const [products, setProducts] = useState([]);
    //const params = useParams();

    useEffect(() => {
        //console.log("id of product is", params.id);
        getCartpro();

    }, []);

    
    

    const getCartpro = async () => {
        let user=localStorage.getItem('user');
        let userobj=JSON.parse(user);
      //console.log(userobj);
   
        let userid=userobj._id;
        let result= await fetch(`http://localhost:5000/add-card/${userid}`);
        result=await result.json();
        setProducts(result);
    }
    
    const deletecart=async(Productid)=>{
        let resultcart= await fetch(`http://localhost:5000/add-card/${Productid}`,{
        method:"Delete"
       });
       resultcart= await resultcart.json()
       if(resultcart)
       {
        alert("Record Deleted Successfully...");
        getCartpro();
       }
    }


    return (
        
        <div className='container'>
                {/* {
                    products?.map((item) =>
                        <>
                            <div className='cart-page'>
                                <h2>Cart-Page</h2>
                                <div className='row'>
                                    <div className='col-sm-8 details'>
                                        <ul>
                                            <li>
                                                <img src={item.Image} alt=".../" />
                                            </li>
                                            <li>
                                                <span><em>Name:{item.ProductName}</em></span>
                                            </li>
                                            <li>
                                                <span><em>Price :₹{item.Price}</em></span>
                                            </li>
                                            <li>
                                                <span>Quantity :1</span>
                                            </li>
                                            <li>
                                                <button className='remove'>Remove from cart</button>
                                            </li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                            </>
                    )
                }
                             */}
                <div className='cart-page'>
                <h2>Cart-Page</h2>
                    <div className='row'>
                     <div className='col-sm-8 details'>
                     <ul>
                                            <li>
                                                {/* <img src="/" alt=".../#" /> */}
                                                <span>Image</span>
                                            </li>
                                            <li>
                                                <span><em>Name</em></span>
                                            </li>
                                            <li>
                                                <span><em>Price :₹</em></span>
                                            </li>
                                            <li>
                                                <span>Quantity </span>
                                            </li>
                                            <li>
                                                <span>Action</span>
                                            </li>
                                        </ul>

                     {
                      
                        products.map((item)=>
                         <>
                    
                                        <ul>
                                            <li>
                                                <img src={item.Image} alt=".../" />
                                            </li>
                                            <li>
                                                <span><em>Name:{item.ProductName}</em></span>
                                            </li>
                                            <li>
                                                <span><em>Price :₹{item.Price}</em></span>
                                            </li>
                                            <li>
                                                <span>Quantity :1</span>
                                            </li>
                                            <li>
                                                <button onClick={()=>deletecart(item.Productid)} className='remove' >Cart<DeleteIcon/></button>
                                            </li>
                                        </ul>
                                    
                            </>
                        )     
                     }
                                 </div>
                                </div>
                            </div>
                            <div>
                               
                                <button className='btn btn-primary'><Link className="nav-link" to="/check-out">Check-Out</Link></button>
                            </div>
                            <div class="col-sm-4 summary">
                                <h3>Summary</h3>
                                <ul>
                                    <li><span>Amount</span><span>₹1500</span></li>
                                    <li><span>Tax</span><span></span></li>
                                    <li><span>Delivery</span><span></span></li>
                                    <li><span>Discount</span><span></span></li>
                                    <li><span><h4>Total</h4></span><span><h4>₹1500</h4></span></li>

                                </ul>
                            </div>
        </div>
                        
          
    )
}
